$primaryColorMain: #4ba2cc;
$primaryColorDark: #20578d;
$primaryColorLight: #f5fafd;

@font-face {
  font-family: "Regular";
  src: url("./assets/fonts/UniNeueRegular.otf"); /* Safari, Android, iOS */
}

@font-face {
  font-family: "Bold";
  src: url("./assets/fonts/UniNeueBold.otf"); /* Safari, Android, iOS */
}

@font-face {
  font-family: "Heavy";
  src: url("./assets/fonts/UniNeueHeavy.otf"); /* Safari, Android, iOS */
}

.regular {
  font-family: "Regular";
}

.bold {
  font-family: "Bold";
}
.heavy {
  font-family: "Heavy";
}

.navlink {
  transition: color 0.2s;
  text-decoration: none;
  padding: 24px;
  color: $primaryColorMain;
  font-size: 1.125rem;
}

.navlink.selected:after {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 50%;
  margin-top: 8px;
  background-color: $primaryColorDark;
  display: block;
}

.navlink:after {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 50%;
  margin-top: 8px;
  background-color: inherit;
  display: block;
}

.navlink:hover {
  color: $primaryColorDark;
}

.navlink.selected {
  color: $primaryColorDark;
}

.rightsideNavigation .itemContainer .item.selected {
  background-color: $primaryColorDark !important;
  // width:12px;
  // height:12px;
  transform: scale(1.5, 1.5);
}

.App {
  //max-width: 1224px;
  //width: 100%;
  //padding: 0 24px 0 24px;
  position: relative;
  margin: auto;
  flex: 1 0 auto;
}

p {
  font-family: "Regular";
  color: $primaryColorMain;
}

.rightsideNavigation {
  display: flex;
  align-items: flex-end;
  position: relative;
  //padding: 24px;
  top: 50%;
  //right: 0px;
  max-width: 1224px;
  position: fixed;
  flex-direction: column;
}

.rightsideNavigationContainer {
  max-width: 1224px;
  width: 100%;
  margin: auto;
  height: 5px;
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.rightsideNavigation .itemContainer {
  width: 12px;
  height: 12px;
  margin: 12px 0 12px 0;
}
.rightsideNavigation .itemContainer .item {
  margin: auto;
  transition: transform 0.2s, background-color 0.2s;
  //padding:4px;
  width: 8px;
  height: 8px;
  border-radius: 45px;
  background-color: $primaryColorMain;
}

html{
  scroll-behavior: smooth;
}
.App {
  scroll-behavior: smooth;
  .navplaceholder {
    height: 96px;
    width: 100%;
    margin-bottom: 200px;
  }

  .header {
    display: flex;
    justify-content: center;
    padding: 0 64px;
    box-sizing: border-box;

    .nav {
      display: flex;
      justify-content: space-between;
      max-width: 960px;
      width: 100%;
      height: 96px;
      align-items: center;
    }
  }
}

.desktopMenu {
  display: flex;
}

.mobileMenu {
  display: none;
}
@media screen and (max-width: 960px) {
  .App {
    .navplaceholder {
      margin-bottom: 64px;
    }
  }
  .desktopMenu {
    display: none;
  }

  .mobileMenu {
    display: inline;
  }
}
