$primaryColorMain: #4ba2cc;
$primaryColorDark: #20578d;
$primaryColorLight: #e3f0f7;
$backgroundcolor: #f4f9fc;
$sensorIconSize: 64px;
$sensorIconSizeHalf: 32px;

.heading {
  color: $primaryColorDark;
}

.explore {
  background-image: repeating-linear-gradient(#f4f9fc, #f4f9fc, #f4f9fc);
  background-repeat: no-repeat;

  .body {
    display: flex;
    justify-content: space-between;

    .left {
      display: inline-block;
    }

    .right {
      display: inline-block;
      position: relative;
      line-height: 1.8;
      max-width: 250px;
    }
  }

  .title {
    display: flex;
    align-items: center;
    font-size: 5rem;
    letter-spacing: 20px;
    margin-bottom: 96px;
    font-family: "Bold";
    color: $primaryColorLight;
    margin-bottom: 128px;
    position: relative;
    top: -3.4rem;
  }
}

@media screen and (max-width: 960px) {
  .explore {
    background-size: 100% 62.5%;
    .body {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      align-items: center;

      .left {
        display: inline-block;
      }

      .right {
        display: inline-block;
        position: relative;
        text-align: center;
        max-width: 260px;
        font-size: 0.875rem;
      }
    }
    .title {
      font-size: 2.5rem;
      justify-content: center;
    }
  }
}

.exploreContainer {
  display: flex;
  justify-content: space-between;
  position: relative;
  align-items: flex-start;
}

.sensorPresentation {
  position: relative;
  max-width: 440px;
  width: 100%;
}

.sensorIcon {
  width: $sensorIconSize;
  height: $sensorIconSize;
  position: absolute;
}

.sensorTop {
  top: -$sensorIconSize;
  left: calc(50% - #{$sensorIconSize/2});
}

.sensorTopRight {
  top: #{$sensorIconSize/2};
  right: 0px;
}

.sensorBottomRight {
  top: #{$sensorIconSize * 4};
  right: 0px;
}

.sensorBottom {
  left: calc(50% - #{$sensorIconSize/2});
  bottom: calc(#{$sensorIconSize} - #{$sensorIconSize/4});
}

.sensorBottomLeft {
  top: #{$sensorIconSize * 4};
  left: 0px;
}

.sensorTopLeft {
  top: #{$sensorIconSize/2};
  left: 0px;
}

.explore.section {
  background-color: $backgroundcolor;
  z-index: -1;
}
