$primaryColorMain: #4ba2cc;
$primaryColorDark: #20578d;
$primaryColorLight: #e3f0f7;
$backgroundcolor: #f4f9fc;
$sensorIconSize: 64px;
$sensorIconSizeHalf: 32px;

.heading {
  color: $primaryColorDark;
}
.footer {
  background-color: "#4AA1CB";
  padding: 48px 0;

  .body {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    max-width: 960px;
    margin: auto;
    .socialandcontact {
      display: flex;
      flex-wrap: wrap;
      width: 100%;

      .logo {
        width: 100%;
      }

      .followandcontact {
        display: flex;
        justify-content: space-between;
        width: 80%;
        .social {
        }
      }
    }
    .subscription {
      position: relative;
      width: 100%;
      top: -16px;
      .input {
        display: flex;
        margin-bottom: 48px;
        input {
          margin-bottom: 24px;
          width: 100%;
          border-radius: 45px;
          padding: 16px 24px 16px 24px;
          border: none;
          color: rgba(32, 87, 141, 0.75);
          font-family: "heavy";
        }
      }
      .win {
        text-align: right;
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .footer {
    .body {
      flex-direction: column-reverse;
      align-items: center;
      padding: 0 24px;

      .socialandcontact {
        max-width: 360px;
        .logo {
          display: none !important;
        }
        .followandcontact {
          width: 100%;
        }
      }

      .subscription {
        display: flex;
        flex-direction: column;
        align-items: center;
        .win {
          text-align: center;
        }
        .input {
          flex-direction: column;
          align-items: center;
          width: 100%;
          max-width: 302px;

          input {
          }
        }
      }
    }
  }
}

@media screen and (max-width: 375px) {
  .footer {
    .body {
      flex-direction: column-reverse;
      align-items: center;
      padding: 0 24px;

      .socialandcontact {
        max-width: 360px;
        .followandcontact {
          flex-direction: column;
          align-items: center;
          text-align: center;
        }
        .logo {
          display: none !important;
        }
      }

      .subscription {
        display: flex;
        flex-direction: column;
        align-items: center;
        .win {
          text-align: center;
        }
        .input {
          flex-direction: column;
          align-items: center;
          input {
            max-width: px;
          }
        }
      }
    }
  }
}

.bigletter {
  font-size: 740px;
  position: absolute;
  //top: -305,
  z-index: -1;
  left: -80px;
  top: -240px;
  color: $backgroundcolor;
}
