$primaryColorMain: #4ba2cc;
$primaryColorDark: #20578d;
$primaryColorLight: #e3f0f7;
$backgroundcolor: #f4f9fc;
$sensorIconSize: 64px;
$sensorIconSizeHalf: 32px;

.heading {
  color: $primaryColorDark;
}
.posture {
  .body {
    display: flex;
    justify-content: space-between;
    position: relative;
    align-items: center;

    .images {
      display: flex;
    }
    .showOnMobile {
      display: none;
    }

    .navigation {
      cursor: pointer;
    }
  }
  .title {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 5rem;
    letter-spacing: 20px;
    font-family: "Bold";
    color: $primaryColorLight;
    position: relative;
  }
}

@media screen and (max-width: 960px) {
  .posture {
    .body {
      display: flex;
      justify-content: space-between;
      position: relative;
      align-items: center;
      flex-direction: column;

      .images {
        order: -1;
      }

      .showOnDesktop {
        display: none;
      }

      .showOnMobile {
        display: flex;
        align-items: baseline;

        p {
          padding: 48px;
        }
      }
    }
    .title {
      font-size: 2.5rem;
      justify-content: center;
    }
  }
}

.bigletter {
  font-size: 740px;
  position: absolute;
  //top: -305,
  z-index: -1;
  left: -80px;
  top: -240px;
  color: $backgroundcolor;
}
