@font-face {
  font-family: "Regular";
  src: url("./assets/fonts/UniNeueRegular.otf"); /* Safari, Android, iOS */
}

@font-face {
  font-family: "Bold";
  src: url("./assets/fonts/UniNeueBold.otf"); /* Safari, Android, iOS */
}

.regular{
  font-family: "Regular"
}

.bold{
  font-family: "Bold"
}

.navlink{
  text-decoration: none
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x:hidden
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
