$primaryColorMain: #4ba2cc;
$primaryColorDark: #20578d;
$primaryColorLight: #f5fafd;

.backgroundDecoration {
  background-color: $primaryColorLight;
  height: 176px;
  width: 95%;
  border-top-right-radius: 90px;
  border-bottom-right-radius: 90px;
  position: absolute;
  left: -50%;
}

.giveawayContainer {
  display: flex;
  position: relative;
  align-items: flex-start;
}

.giveawayHeading {
  display: flex;
  align-items: center;
  font-size: 5rem;
  letter-spacing: 20px;
  margin-bottom: 96px;
  font-family: "Heavy";
  color: $primaryColorMain;
}

.giveawayLeft {
  display: inline-block;
}

.giveawayBody {
  display: flex;
  justify-content: space-between;
}

.giveawayBody {
  .left {
    width: 100%;
    .text {
      line-height: 2.5rem;
      font-size: 1.5rem;
    }
  }

  .right {
    position: relative;
    top: -240px;
    width: 100%;
  }
}

@media screen and (max-width: 960px) {
  .giveaway {

    .giveawayHeading{
      font-size:48px;
      text-align: center;
      justify-content: center;
      .backgroundDecoration{
        left:-25%;
        height:110px;
      }
    }
    .giveawayBody {
      flex-direction: column-reverse;
      align-items: center;
      .left {
        max-width: 362px;
        .text {
          text-align: center;
          font-size: 1.125rem;
          line-height: 1.5rem;
        }
      }
      .right {
        position: static;
        max-width: 362px;
      }
    }

    .giveawayLeft {
      max-width: 360px;
    }
  }
}
