$primaryColorMain: #4ba2cc;
$primaryColorDark: #20578d;
$primaryColorLight: #e3f0f7;
$backgroundcolor: #f4f9fc;
$sensorIconSize: 64px;
$sensorIconSizeHalf: 32px;

.heading {
  color: $primaryColorDark;
}

.stepsBackground {
  background-image: repeating-linear-gradient(#f4f9fc, #f4f9fc, #f4f9fc);
  background-repeat: no-repeat;
  background-size: 100% 50%;
  background-position-y: 130%;
  padding-bottom: 124px;
}

.steps {
  .body {
    padding:0 16px;
    display: flex;
    justify-content: space-between;
    position: relative;
    align-items: flex-start;
    max-width: 960px;
    margin: auto;
  }
  .images {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    width: 100%;
  }

  .title {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    font-size: 5rem;
    letter-spacing: 20px;
    font-family: "Bold";
    color: $primaryColorLight;
    position: relative;
  }
}

@media screen and (max-width: 960px) {
  .stepsBackground {
    background-position-y: 100%;
  }
  .steps {
    max-width: 456px;
    margin: auto;

    .body {
      .images {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100%;

        #step2 {
          align-self: flex-end;
        }
      }
    }

    .title {
      font-size: 2.5rem;
      justify-content: center;
    }
  }
}

.steps--bigletter {
  font-size: 460px;
  position: absolute;
  //top: -305,
  z-index: -2;
  left: -80px;
  top: -240px;
  color: $backgroundcolor;
}
