$primaryColorMain: #4ba2cc;
$primaryColorDark: #20578d;
$primaryColorLight: #f5fafd;

.button.dark {
  background-color: $primaryColorDark;
}

.button.light {
  background-color: $primaryColorMain;
}

.button.root {
  padding:0;
  border-radius: 45px;
  height: 48px;
  width: 166px;
  //margin: 8px;
  font: 0.875rem "Heavy";
  color: white;
  border:0;
  cursor: pointer;
}

.button:focus {
  outline: 0;
}

