//   transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

//   background-color: lighten($primaryColorMain, 10%) !important;
// }

.button.dark:hover {
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

  background-color: darken($primaryColorDark, 10%);
}
.button.light:hover {
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

  background-color: darken($primaryColorMain, 10%);
}
