$primaryColorMain: #4ba2cc;
$primaryColorDark: #20578d;
$primaryColorLight: #f5fafd;
$dotColor: #48A2CD;

$horizontalMargin: 7.1px;
$verticalMargin:8.5px;

.dot{
  width:5px;
  height:5px;
  background-color: $dotColor;
  opacity:0.1;
  margin-left:$horizontalMargin;
  margin-right:$horizontalMargin;
  margin-top:$verticalMargin;
  margin-bottom:$verticalMargin;
  border-radius:50%;
  display:inline-block;
}